*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1;
    display: flex; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 35px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 35px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

.header {
  position: relative;
  z-index: 0;
  display: block;
  background-color: #ffffff; }
  .header .container {
    display: flex;
    flex-direction: column; }

.header__wrapper {
  height: 74px; }

.header__logo {
  padding-top: 25px;
  display: block;
  align-self: start; }
  .header__logo svg {
    width: 192px;
    height: 24px; }

@keyframes loaderInit {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.loader {
  position: absolute;
  z-index: 1;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: #ffffff;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1); }
  .loader.hidden {
    opacity: 0; }

.loader__logo {
  width: 801px;
  height: 100px;
  animation-duration: 1.2s;
  animation-name: loaderInit; }
  @media (max-width: 1023px) {
    .loader__logo {
      width: 550px;
      height: 70px; } }

.loader__desc {
  position: absolute;
  bottom: 103px;
  opacity: 0;
  margin: 0;
  font-size: 24px;
  line-height: 22px;
  animation-duration: 1.2s;
  animation-name: loaderInit;
  animation-delay: 0.6s; }
  .loader__desc .tablet {
    display: none; }
  @media (max-width: 1023px) {
    .loader__desc .tablet {
      display: inline; }
    .loader__desc .desktop {
      display: none; } }

.main {
  position: relative;
  padding-top: 46px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }
  .main h1 {
    margin: 0;
    padding-top: 59px;
    width: 576px;
    flex-shrink: 0;
    transform: translateY(15px);
    font-weight: normal;
    font-size: 96px;
    line-height: 96px;
    color: #2c39f2;
    opacity: 0;
    transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s ease; }
  .main .container {
    position: relative;
    display: flex;
    min-height: 648px; }
  .main.init .main__text-wrapper {
    opacity: 1;
    transform: translateY(0); }
  .main.init .main__bg {
    opacity: 1; }
  .main.init h1 {
    opacity: 1;
    transform: translateY(0); }
  @media (max-width: 1023px) {
    .main {
      cursor: pointer; }
      .main h1 {
        width: 100%;
        pointer-events: none; }
      .main .container {
        position: static; }
      .main.init .main__text-wrapper {
        opacity: 1;
        transform: translateX(-100%);
        transition: transform 0.6s cubic-bezier(0.55, 0, 0.1, 1); }
        .main.init .main__text-wrapper.active {
