          transform: translateX(0); }
      .main.init .main__bg {
        opacity: 1; }
      .main.init h1 {
        opacity: 1;
        transform: translateY(0); } }

.main__text-wrapper {
  width: 384px;
  padding-left: 26px;
  flex-shrink: 0;
  opacity: 0;
  transform: translateY(15px);
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s ease;
  transition-delay: 0.6s; }
  .main__text-wrapper .main__svg {
